import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents';

const RatingCalculated = (props) => {
    const { currentPage, setCurrentPage, ratingCalculatedData } = props

    const ratingCalculatedField = [
        { key: 'sn', label: 'S.N' },
        { key: 'kraId', label: 'KRA ID' },
        { key: 'kraParameterId', label: 'KRA Parameter Id' },
        { key: 'email', label: 'Email' },
        { key: 'reportingManager', label: 'Reporting Manager' },
        { key: 'rating', label: 'Rating' },
        { key: 'month', label: 'Month' },


    ];
    const ratingCalculatedScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),


    }), [ratingCalculatedData]);
    return (
        <div>
            <div className="table-responsive tableDiv text-nowrap">
                <CustomTable
                    data={ratingCalculatedData || []}
                    fields={ratingCalculatedField}
                    // isLoading={isLoading}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={ratingCalculatedData?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={ratingCalculatedScopedSlots}
                />
            </div>
        </div>
    )
}

export default RatingCalculated
