import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents';

const RatingDetails = (props) => {
    const { currentPage, setCurrentPage, kraData } = props

    const ratingDetailField = [
        { key: 'sn', label: 'S.N' },
        { key: 'kraId', label: 'KRA ID' },
        { key: 'email', label: 'Email' },
        { key: 'reportingManager', label: 'Reporting Manager' },
        { key: 'averageRatingTotal', label: 'Average Rating Total' },
        { key: 'averageRating', label: 'Average Rating' },
        { key: 'month', label: 'Month' },

     
    ];
    const ratingDetailScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
    }), [kraData]);
    return (
        <div>
            <div className="table-responsive tableDiv text-nowrap">
                <CustomTable
                    data={kraData || []}
                    fields={ratingDetailField}
                    // isLoading={isLoading}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={kraData?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={ratingDetailScopedSlots}
                />
            </div>
        </div>
    )
}

export default RatingDetails
